@import "normalize.scss";
@import "reset.scss";

@import url('http://fonts.cdnfonts.com/css/circe');

* {
  box-sizing: border-box; }

body {
  font-family: 'Circe', sans-serif;
  font-weight: 400; }

.container {
  margin: 0 auto;
  width: 1218px; }

.circle {
  border-radius: 100%;
  border: none;
  z-index: -1; }

.title {
  font-size: 46px;
  font-family: 'Circe', sans-serif;
  line-height: 120%;
  font-weight: 700; }

.text {
  font-weight: 400;
  font-size: 26px;
  line-height: 130%; }

@keyframes opacity_animation {
  from {
    display: block;
    opacity: 0;
    transform: scale(0.2); }
  to {
    opacity: 1;
    transform: scale(1); } }



@keyframes rotate_animation {
  from {
    transform: rotate(0deg); }
  40% {
    transform: rotate(-9deg); }
  80% {
    transform: rotate(8deg); }
  to {
    transform: rotate(0deg); } }

@keyframes rotate_animation_micro {
  from {
    transform: rotate(9deg); }
  40% {
    transform: rotate(15deg); }
  80% {
    transform: rotate(-15deg); }
  to {
    transform: rotate(9deg); } }

@keyframes card1 {
  from {
    opacity: 0;
    transform: scale(0.2); }
  to {
    opacity: 1;
    transform: scale(1); } }

@keyframes card2 {
  from {
    opacity: 0;
    transform: scale(0.2); }
  to {
    display: block;
    opacity: 1;
    transform: scale(1); } }

@keyframes cards_animation {
  from {
    transform: translate(0px);
    opacity: 0; }
  50% {
    transform: translate(0px, 30px);
    opacity: 1; }
  to {
    transform: translate(0px);
    opacity: 1; } }






